<template>
    <v-footer
        color="white"
        dark
        class="d-flex flex-column justify-center onepage-footer">
            <div class="my-5">
                <v-btn
                    v-for="icon in icons"
                    :key="icon"
                    class="scroll-out-item-bottom-sp mx-4"
                    icon>
                    <v-icon size="30px" color="#6e51e6">{{ icon }}</v-icon>
                </v-btn>
            </div>
            <p class="onepage-footer-text scroll-out-item-center">
                Copyright © {{ new Date().getFullYear() }} 
                <span style="color: #6e51e6; font-weight: 700;">CHATAPP</span> - Design: JProject
            </p>
    </v-footer>
</template>

<style scoped>
    .onepage-footer {
        height: 30vh;
    }
    .onepage-footer-text {
        font-family: 'Poppins', sans-serif;
        font-weight: 600;
        color: #5d5f86;
        word-spacing: 0.4em;
    }
    @media only screen and (max-width: 960px) {
        .onepage-footer {
            text-align: center;
        }
    }
</style>

<script>
import ScrollOut from "scroll-out";
import { mapGetters } from "vuex";
    export default {
        name: 'Footer',

        mounted() {
            this.so = ScrollOut({
                scope: this.$el,
                once: this.getScrollOutOnce,
                threshold: 0.5,
                targets:
                    ".scroll-out-item-center, .scroll-out-item-bottom-sp",
            });
        },
        
        destroyed() {
            this.so.teardown();
        },

        computed: {
            ...mapGetters({
                getScrollOutOnce: "getScrollOutOnce",
            }),
        },

        data: () => ({
            icons: [
                'mdi-facebook',
                'mdi-twitter',
                'mdi-linkedin',
                'mdi-instagram',
            ],
        }),
    }
</script>